import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Icon from '@material-ui/core/Icon';
import TextField from '@material-ui/core/TextField';
import { GetPlcStatus, GetPlcDataPlcPublic } from '../../layout/GetData/FirebaseGetData'
import { actionWritePLC } from '../../../redux/actions/mobileRobotAction'
import CommandPC from '../../speechRecognition/CommandPC';
import CommandMobile from '../../speechRecognition/CommandMobile';
import { actionToggleModeSpeech } from '../../../redux/actions/authAction'
const styles = {
  card: {
    minWidth: 275,
    maxWidth: 1200,
    justifyContent: 'center'
  },
  textField: {
    width: '100%'
  },
}
class DashboardMobile extends Component {
  constructor(props) {
    super(props)
    this.state = {
      statusConnectPLC: 'No signal',
      dataFromPlc: '',
      commandTyping: '',
      goalLocallized: ''
    }
  }
  componentWillMount() {
    GetPlcStatus(result => {
      console.log(result)
      this.setState({ statusConnectPLC: result })
    })
    GetPlcDataPlcPublic(result => {
      this.setState({ dataFromPlc: result })
    })
  }
  onGetCommand = (cb) => {
    let arr = cb.toLowerCase().split(' ');
    let thietbi = '';
    let lenh = '';
    for (let i = 0; i < arr.length; i++) {
      if (arr[i] === 'robot') thietbi = 'mobile_robot';
      if (arr[i] === '1' || arr[i] === 'một') lenh = 'diem1';
      if (arr[i] === '2' || arr[i] === 'hai') lenh = 'diem2';
      if (arr[i] === '3' || arr[i] === 'ba') lenh = 'diem3';
      if (arr[i] === '4' || arr[i] === 'bốn') lenh = 'diem4';
      if (arr[i] === '5' || arr[i] === 'năm') lenh = 'diem5';
      if (arr[i] === 'sạc' || arr[i] === 'xạc') lenh = 'dock';
    }
    if (thietbi === 'mobile_robot' && lenh === 'diem1') this.props.actionWritePLC('2Patrolonce Route1\r\n')
    if (thietbi === 'mobile_robot' && lenh === 'diem2') this.props.actionWritePLC('2Patrolonce Route2\r\n')
    if (thietbi === 'mobile_robot' && lenh === 'diem3') this.props.actionWritePLC('2Patrolonce Route3\r\n')
    if (thietbi === 'mobile_robot' && lenh === 'diem4') this.props.actionWritePLC('2Patrolonce Route4\r\n')
    if (thietbi === 'mobile_robot' && lenh === 'diem5') this.props.actionWritePLC('2Patrolonce Route5\r\n')
    if (thietbi === 'mobile_robot' && lenh === 'dock') this.props.actionWritePLC('2dock\r\n')
  }
  buttonChoiseModeSpeech = () => {
    return (this.props.modeSpeech === 1 ? <Button><Icon>mobile_screen_share</Icon>Mobile</Button> : <Button><Icon>desktop_mac</Icon>PC</Button>)
  }
  render() {
    const { classes } = this.props
    const { uid } = this.props;
    if (!uid) {
      return <Redirect to='/signin' />
    }
    return (
      <Grid container alignItems='center' justify='center' style={{ height:'100vh',overflow: 'auto',background: 'linear-gradient(to top, #30cfd0 0%, #330867 100%)' }}>
        <Card className={classes.card} style={{background: 'linear-gradient(-225deg, #5D9FFF 0%, #B8DCFF 48%, #6BBBFF 100%)' }}>
          <CardContent>
          <TextField 
              id="outlined-multiline-static"
              label="Data from PLC"
              multiline
              rows="5"
              value={this.state.dataFromPlc}
              className={classes.textField}
              margin="normal"
              variant="outlined"
            />
            
            <div className="dropdown-divider"></div>
            <div style={{ justifyContent: 'center', display: 'flex' }}>
              PLC-SERVER:{this.state.statusConnectPLC === 'Connected' ? <Icon className={classes.rightIcon}>done_outline</Icon> : <Icon className={classes.rightIcon}>error</Icon>}
            </div>
            <div className="dropdown-divider"></div>
            <div style={{ justifyContent: 'center', display: 'flex', width: '100%' }} onClick={this.props.actionToggleModeSpeech}>{this.buttonChoiseModeSpeech()}</div>
            {this.props.modeSpeech === 1 ? <CommandMobile commandOut={(cb) => this.onGetCommand(cb)} /> : <CommandPC commandOut={(cb) => this.onGetCommand(cb)} />}
            <div className="dropdown-divider"></div>
            {this.props.idUser === 'S5sPGpvaNuWQXms4iPH8VXo3pu93' ? <div style={{ justifyContent: 'center', display: 'flex', color: 'red' }}>{this.props.lastCommand}</div> : ''}
            <div className="dropdown-divider"></div>
            
          </CardContent>
          <CardActions>
            <Button variant="outlined" color="primary" onClick={() => this.props.actionWritePLC('1')}>CONNECT<Icon className={classes.rightIcon}>router</Icon></Button>
          </CardActions>
          <CardActions>
            <Button variant="outlined" color="primary" onClick={() => this.props.actionWritePLC('2Patrolonce Route1')}>GO TO POINT 1  <Icon className={classes.rightIcon}>send</Icon></Button>
            <Button variant="outlined" color="primary" onClick={() => this.props.actionWritePLC('2Patrolonce Route2')}>GO TO POINT 2  <Icon className={classes.rightIcon}>send</Icon></Button>
            <Button variant="outlined" color="primary" onClick={() => this.props.actionWritePLC('2Patrolonce Route3')}>GO TO POINT 3  <Icon className={classes.rightIcon}>send</Icon></Button>
            <Button variant="outlined" color="primary" onClick={() => this.props.actionWritePLC('2Patrolonce Route4')}>GO TO POINT 4  <Icon className={classes.rightIcon}>send</Icon></Button>
            <Button variant="outlined" color="primary" onClick={() => this.props.actionWritePLC('2Patrolonce Route5')}>GO TO POINT 5  <Icon className={classes.rightIcon}>send</Icon></Button>
            <Button variant="outlined" color="primary" onClick={() => this.props.actionWritePLC('5')}>STOP  <Icon className={classes.rightIcon}>stop</Icon></Button>
          </CardActions>
          <div className="dropdown-divider"></div>
          <small id="src" className="form-text text-muted">SAY:</small>
          <input style={{ width: '95%', margin: 'auto' }} onChange={(evt) => { this.setState({ commandTyping: evt.target.value }) }} type="text" className="form-control" name="src" id="src" aria-describedby="helpIdTitle" placeholder="SAY SOMETHING" />

          <Grid>
            <CardActions style={{ justifyContent: 'center' }}>
              <Button variant="outlined" color="primary" onClick={() => this.props.actionWritePLC('3')}>ENABLE MOTOR<Icon className={classes.rightIcon}>power</Icon></Button>
              <Button variant="outlined" color="primary" onClick={() => this.props.actionWritePLC('6')}>STATUS  <Icon className={classes.rightIcon}>search</Icon></Button>
              <Button variant="outlined" color="primary" onClick={() => this.props.actionWritePLC('2Patrol Route4\r\n')}>LOOP  <Icon className={classes.rightIcon}>loop</Icon></Button>
              <Button variant="outlined" color="primary" onClick={() => this.props.actionWritePLC('2dock\r\n')}>GO TO DOCK  <Icon className={classes.rightIcon}>pin_drop</Icon></Button>
              <Button variant="outlined" color="primary" padding='5px' onClick={() => this.props.actionWritePLC('4' + this.state.commandTyping)}>SAY  <Icon className={classes.rightIcon}>mic</Icon></Button>
            </CardActions>
          </Grid>
          <Grid>
            <CardActions style={{ justifyContent: 'center' }}>
              <Button variant="outlined" color="primary" onClick={() => this.props.actionWritePLC('2Patrolonce CartCapture\r\n')}>CART CAPTURE  <Icon className={classes.rightIcon}>shopping_cart</Icon></Button>
              <Button variant="outlined" color="primary" onClick={() => this.props.actionWritePLC('2Patrolonce CartRelease\r\n')}>CART RELEASE  <Icon className={classes.rightIcon}>shopping_cart</Icon></Button>
              <Button variant="outlined" color="primary" onClick={() => this.props.actionWritePLC('2Patrolonce TurnLeft\r\n')}>TURN LEFT  <Icon className={classes.rightIcon}>chevron_left</Icon></Button>
              <Button variant="outlined" color="primary" onClick={() => this.props.actionWritePLC('2Patrolonce TurnRight\r\n')}>TURN RIGHT  <Icon className={classes.rightIcon}>chevron_right</Icon></Button>
              <Button variant="outlined" color="primary" onClick={() => this.props.actionWritePLC('2Patrolonce MoveBack\r\n')}>MOVE BACK  <Icon className={classes.rightIcon}>keyboard_arrow_down</Icon></Button>
              <Button variant="outlined" color="primary" onClick={() => this.props.actionWritePLC('2Patrolonce MoveUp\r\n')}>MOVE UP  <Icon className={classes.rightIcon}>keyboard_arrow_up</Icon></Button>
              <Button variant="outlined" color="primary" onClick={() => this.props.actionWritePLC('2deltaHeading (-10,0,0,0,30,0,True,False,0)')}>TEST  <Icon className={classes.rightIcon}>keyboard_arrow_up</Icon></Button>
            </CardActions>
          </Grid>
          <Grid>
            <div className="dropdown-divider"></div>
            <input style={{ width: '70%', margin: 'auto' }} onChange={(evt) => { this.setState({ goalLocallized: evt.target.value }) }} type="text" className="form-control" name="src" id="src" aria-describedby="helpIdTitle" placeholder="GOAL" />
            <CardActions style={{ justifyContent: 'center' }}>
              <Button variant="outlined" color="primary" onClick={() => this.props.actionWritePLC('7' + this.state.goalLocallized)}>Locallized at Goal  <Icon>room</Icon></Button>
            </CardActions>
          </Grid>
        </Card>
      </Grid>
    )
  }
}
const mapStateToProps = (state, ownProps) => {
  return {
    statusConnectPLC: state.mobileRobot.statusConnect,
    lastCommand: state.mobileRobot.lastCommand,
    modeSpeech: state.auth.modeSpeech,
    idUser: state.firebase.auth.uid,
    uid: state.firebase.auth.uid
  }
}
const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    actionWritePLC: (cmd) => {
      dispatch(actionWritePLC(cmd))
    },
    actionToggleModeSpeech: () => {
      dispatch(actionToggleModeSpeech())
    },
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(DashboardMobile))