import React, { Component } from 'react'
import SpeechRecognition from 'react-speech-recognition'
import { withStyles } from '@material-ui/core/styles';
import Icon from '@material-ui/core/Icon';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
var a = null;
const styles = theme => ({
    button: {
        margin: theme.spacing.unit,
    },
    root: {
        flexGrow: 1,
    },
});
class CommandMobile extends Component {
    mySetTimeOut = (fun1, fun2, result) => {
        a = setTimeout(() => {
            this.props.commandOut(result);
            fun1();
            fun2();
            clearTimeout(a);
        }, 1000)
    }
    myUpdateTimeOut = () => {
        if (a !== null) clearTimeout(a);
    }
    render() {
        const { classes } = this.props
        const { abortListening, interimTranscript, finalTranscript, recognition, startListening, stopListening, listening, transcript, resetTranscript, browserSupportsSpeechRecognition } = this.props

        if (!browserSupportsSpeechRecognition) {
            return <div style={{'justifyContent':'center','display':'flex'}}>Browser not support SpeechRecognition</div>
        }
        recognition.lang = 'vi-VN';
        if (interimTranscript !== '') {
            this.myUpdateTimeOut();
            this.mySetTimeOut(abortListening, resetTranscript, interimTranscript);
        }
        if (finalTranscript !== '') {
            resetTranscript()
        }
        return (
            <div>
                <Grid className={classes.root} container justify='center' alignItems='center' direction='column'>
                    <Grid>
                        {listening ? <Icon>mic</Icon> : <Icon>mic_off</Icon>}
                    </Grid>
                    <Grid>
                        <Button onClick={resetTranscript} variant="outlined" color="primary" className={classes.button}><Icon>refresh</Icon>Reset</Button>
                        <Button onClick={startListening} ariant="outlined" color="primary" className={classes.button}><Icon>play_arrow</Icon>Start Record</Button>
                        <Button onClick={stopListening} variant="outlined" color="primary" className={classes.button}><Icon>cancel</Icon>Stop Record</Button>
                    </Grid>
                    <Grid>
                        <span>.{transcript}.</span>
                    </Grid>
                </Grid>
            </div>

        )
    }
}
const options = {
    autoStart: false,
    continuous: false
}

export default withStyles(styles)(SpeechRecognition(options)(CommandMobile))